.Saved {
  min-height: 1000px;
  padding-top: 65px;
  &__title {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 50px;
    line-height: 61px;
    display: flex;
    align-items: center;
    margin-bottom: 100px;

    color: #000000;
  }
  &__empty {
    position: relative;
    display: flex;
  }
  &__like {
    position: absolute;
    top: -85%;
    font-size: 54px;
    color: #d75a4a;
  }

  &__paragraph {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 30px;
    color: #898989;
    margin-right: 115px;
  }
  &__button {
    padding: 20px 85px;
    background: #ffffff;
    border: 1px solid #cd3232;
    box-sizing: border-box;
    box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    display: flex;
    align-items: center;
    text-align: center;

    color: #cd3232;
  }
}
