.Profile {
  &__avatar {
    float: left;
    margin-right: 35px;
    margin-bottom: 50px;
    width: 167px;
    height: 167px;
    position: relative;
    &--image {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
    &--label {
      color: #fff;
      font-size: 36px;
    }
    &--edit {
      width: 0.1px;
      height: 0.1px;
      opacity: 0;
      overflow: hidden;
      position: absolute;
      z-index: -1;
    }
    &--overlay {
      opacity: 0;
      position: absolute;
      border-radius: 50%;
      width: 100%;
      height: 100%;
      top: 0;
      background: rgba(0, 0, 0, 0.5);
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 0.5s;
    }
    &:hover .Profile__avatar--overlay {
      opacity: 1;
    }
  }
  &__username {
    margin-top: 100px;
    margin-bottom: 60px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 30px;
    line-height: 37px;
    display: flex;
    align-items: center;
    color: #cd3232;
  }
  &__save {
    background: #cd3232;
    border-radius: 30px;
    padding: 10px 20px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    display: flex;
    align-items: center;
    text-align: center;

    color: #fff;
  }
  &__verified {
    float: right;
  }
  &__input {
    height: 38px;
    border: none;
    &--edit {
      height: 38px;
      width: 100%;
      padding: 2px 8px;
      border-radius: 4px;
      border: 1px solid hsl(0, 0%, 80%);
    }
  }
  &__edit {
    background: #ffffff;
    border: 1px solid #cd3232;
    box-sizing: border-box;
    border-radius: 30px;
    padding: 10px 20px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    display: flex;
    align-items: center;
    text-align: center;

    color: #cd3232;
  }
  &__toggle {
    cursor: pointer;
    margin-left: 50px;
    margin-top: 5px;
    float: right;
    width: 48px;
    height: 28px;
    border-radius: 20px;
    border: 1px solid #cd3232;
    background: #fff;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 1px;
      left: 1px;
      width: 24px;
      height: 24px;
      border-radius: 20px;
      transition: 0.4s;
      background: #cd3232;
    }
    &--active {
      &::after {
        transform: translateX(20px);
      }
    }

    &--block {
      float: right;
    }
    &--label {
      margin-top: 5px;
      font-family: Montserrat;
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      display: flex;
      align-items: center;
      color: #000000;
    }
  }
  &__ad-button {
    width: 100%;
    margin-top: 50px;
    padding: 15px;
    border: 1px solid #cd3232;
    box-sizing: border-box;
    border-radius: 10px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    color: #cd3232;
  }
}
