.Apartments {
  &__image {
    width: 100%;
    height: 50vh;
    object-fit: cover;
  }
  &__main {
    margin-top: 50px;
    // display: grid;
    // grid-template-columns: 70% 30%;
  }
  &__block {
    padding-right: 50px;
  }
  &__title {
    font-size: 32px;
  }

  &__description {
    margin-top: 50px;
    font-size: 18px;
  }

  &__sidebar {
    &--item {
      display: flex;
      align-items: center;
      border: 1px solid #b0b0b0;
      border-radius: 5px;
      padding: 10px;
      margin-bottom: 20px;
    }
    &--image {
      width: 75px;
      height: 75px;
      object-fit: cover;
    }
    &--title {
      margin-left: 20px;
      margin-right: 20px;
      font-size: 20px;
    }
    &--button {
      background: none;
      border: none;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
