.Sidebar {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  margin-right: 60px;
  margin-top: 140px;
  &__button {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    background: #fff;
    display: flex;
    align-items: center;
    text-align: center;
    border: 1px solid rgba(0, 0, 0, 0.8);
    border-bottom: none;
    color: #000000;
    padding: 25px;
    &:first-child {
      border-radius: 5px 5px 0px 0px;
    }
    &:last-child {
      border-radius: 0px 0px 5px 5px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.8);
    }
  }
  &__active {
    background: #cd3232;
    color: #fff;
  }
}
